import React, { Component } from 'react';
import style from './RegisterView.module.css';
import { connect } from 'react-redux';
import { authOperations } from '../../redux/auth';
//import s from '../../components/Button/Button.module.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { authSelectors } from '../../redux/auth';

class RegisterView extends Component {

    state = {
        name: '',
        email: '',
        password: '',
    };

    handleChange = ({ target: { name, value } }) => {
        this.setState({ [name]: value });
    };

    handleSubmit = e => {
        e.preventDefault();
        const { name, email, password } = this.state;
       
        if(name.trim() ===''  ){
           toast.error('–í–≤–µ–¥–∏—Ç–µ –ò–º—è');
        return;
         }
          if (email.trim() ===''){
            toast.error('–í–≤–µ–¥–∏—Ç–µ –ü–æ—á—Ç—É ');
        return;
        }          
       if(password.trim() ===''  ){
           toast.error('–í–≤–µ–¥–∏—Ç–µ –ü–∞—Ä–æ–ª—å');
        return;
       }
        
        this.props.onRegister(this.state);
        this.setState({ name: '', email: '', password: '' })
    };


render() {

    const { name, email, password } = this.state;

        return (
            <div >
                <h1 className={style.title}>–°—Ç—Ä–∞–Ω–∏—Ü–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏</h1>
                <div >
                <form className={style.form}
                    onSubmit={this.handleSubmit}
                    
                        autoComplete='off'>
                        <div >
                    <label  className={style.label}>
                        –ò–º—è  
                        <input
                            type="text"
                            name="name"
                            value={name}
                            onChange={this.handleChange} />
                    </label>
                        </div>
                        <div >
                    <label className={style.label}>
                        –ü–æ—á—Ç–∞  
                        <input 
                            type="email"
                            name="email"
                            value={email}
                            onChange={this.handleChange} />
                            </label>
                        </div>
                        <div >
                    <label className={style.label}>
                        –ü–∞—Ä–æ–ª—å  
                        <input 
                            type="password"
                            name="password"
                            value={password}
                            onChange={this.handleChange} />
                            </label>
                        </div>
                        <div >
                    <button className={style.Button} type="submit" >–ó–∞—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
                        </div>
                        {this.props.isloadingAuth && <p>–ó–∞–≥—Ä—É–∂–∞–µ–º...</p>}
                    </form>
                   </div>
            </div>
        );
    };

};
const mapStateToProps = (state) => ({
    isLoadingAuth: authSelectors.getLoadingAuth(state),
});
const mapDispatchToProps = {
    onRegister: authOperations.register,
};

export default connect(mapStateToProps,mapDispatchToProps)(RegisterView);
