import React, { Component } from 'react';
import styles from './LoginView.module.css';
import { connect } from 'react-redux';
import { authOperations } from '../../redux/auth';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import s from '../../components/Button/Button.module.css'
import { authSelectors } from '../../redux/auth';

class LoginView extends Component {

    state = {
        email: '',
        password: '',
    };

    handleChange = ({ target: { name, value } }) => {
        this.setState({ [name]: value });
    };
    handleSubmit = e => {
        e.preventDefault();
        const { email, password } = this.state;
        
        if (email.trim() ===''){
            toast.error('–í–≤–µ–¥–∏—Ç–µ email ');
        return;
        }          
       if(password.trim() ===''  ){
           toast.error('–í–≤–µ–¥–∏—Ç–µ password');
        return;
         }
            
        this.props.onLogin(this.state);
        this.setState({ name: '', email: '', password: '' })
    };

render() {
    const { email, password } = this.state;
        return (
            <div>
                <h1 className={styles.Title}>–°—Ç—Ä–∞–Ω–∏—Ü–∞ –ª–æ–≥–∏–Ω–∞</h1>
                <form
                    onSubmit={this.handleSubmit}
                    className={styles.Form}
                    autoComplete='off'>
                    <label className={styles.Label}>
                        –ü–æ—á—Ç–∞ <input
                            type="email"
                            name="email"
                            value={email}
                            onChange={this.handleChange} />
                    </label>
                    <label className={styles.Label}>
                        –ü–∞—Ä–æ–ª—å <input
                            type="password"
                            name="password"
                            value={password}
                            onChange={this.handleChange}/>
                    </label>
                    <button className={s.Button} type="submit" >–í–æ–π—Ç–∏</button>
                     {this.props.isloadingAuth && <p>–ó–∞–≥—Ä—É–∂–∞–µ–º...</p>}
                    </form>
            </div>
        );
    };
};
const mapStateToProps = (state) => ({
    isLoadingAuth: authSelectors.getLoadingAuth(state),
});

const mapDispatchToProps = {
    onLogin:authOperations.logIn,
}
export default connect(mapStateToProps,mapDispatchToProps)(LoginView);
